Modal.setAppElement("#___gatsby");

export const query = graphql`
query {
  file(relativePath: {eq: "hayata.jpg"}) {
    id
    childImageSharp {
      fluid(maxWidth: 700) {
        aspectRatio
        base64
        sizes
        src
        srcSet
        srcSetWebp
        srcWebp
      }
    }
  }
}
`

